.chat {
    padding: 30px
}

.messages {
    display: flex;
    flex-direction: column-reverse;
    width: 400px;
    height: 600px;
    background: white;
    overflow: auto;
    direction: revert;
}

.message {
    border: 1px solid black;
    position: relative;
    color: black;
    font-size: 20px;
    margin-bottom: 10px;
    padding: 5px;
    opacity: 0;
    animation: show 1s 1; /* Название, прододжительность, к-ство повторений */
    animation-fill-mode: forwards; /* Чтобы элемент оставался в конечном состоянии анимации */
    animation-delay: 0.1s; /* Задержка перед началом */
}

@keyframes show {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.messageTitle{
    position: relative;
}

.messageDate,
.messageTime {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(-50%, -50%);
    margin-left: 15px;
    font-size: 10px;
}

.messagesImg {
    width: 150px;
}